
.search-filter .input-group-append .btn {
    height: 38px;
}

button.sample{
    padding: 0 0 0.2rem 0;
}
.book {
    cursor: pointer;
}

.book .card-body {
    display: -webkit-flex;
    display: flex;
    padding: 0.3rem;
}

.book .book-image-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 64px;
    min-width: 64px;
    margin-right: 1rem;
}

.book .book-image {
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.book .book-info-container {
    width: 100%;
}

.book .close {
    margin-left: 0.2rem;
}

.book .list-group {
    margin: 1rem 0 0 1rem;
    font-size: 0.85rem;
}

.book .list-group-item {
    padding: .1rem;
    border: none;
}

.book .list-group-item .term {
    margin-right: .5rem;
}

.book .book-info {
    font-size: 12px;
    font-style: italic;
}

.book .book-authors {
    font-size: 14px;
}

.book .book-title{
    font-size: 16px;
    font-weight: 700;
}

.spinner-container{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content: center;
            justify-content: center;
}

@keyframes lds-magnify {
    0% {
      -webkit-transform: translate(2px, 2px);
      transform: translate(2px, 2px);
    }
    33.33% {
      -webkit-transform: translate(102px, 2px);
      transform: translate(102px, 2px);
    }
    66.66% {
      -webkit-transform: translate(42px, 102px);
      transform: translate(42px, 102px);
    }
    100% {
      -webkit-transform: translate(2px, 2px);
      transform: translate(2px, 2px);
    }
  }
  @-webkit-keyframes lds-magnify {
    0% {
      -webkit-transform: translate(2px, 2px);
      transform: translate(2px, 2px);
    }
    33.33% {
      -webkit-transform: translate(102px, 2px);
      transform: translate(102px, 2px);
    }
    66.66% {
      -webkit-transform: translate(42px, 102px);
      transform: translate(42px, 102px);
    }
    100% {
      -webkit-transform: translate(2px, 2px);
      transform: translate(2px, 2px);
    }
  }
  .lds-magnify {
    position: relative;
  }
  .lds-magnify > div {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    -webkit-transform-origin: 100px 100px;
    transform-origin: 100px 100px;
  }
  .lds-magnify > div > div {
    -webkit-animation: lds-magnify 1s linear infinite;
    animation: lds-magnify 1s linear infinite;
    position: absolute;
  }
  .lds-magnify > div > div div:nth-child(1) {
    width: 96px;
    height: 96px;
    border-radius: 50%;
    border: 12px solid #de786a;
    background: #fcddc9;
  }
  .lds-magnify > div > div div:nth-child(2) {
    width: 17px;
    height: 51px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    background: #de786a;
    border-radius: 0 0 8px 8px;
    position: absolute;
    top: 68px;
    left: 85px;
  }
  .lds-magnify {
    width: 100px !important;
    height: 100px !important;
    -webkit-transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
    transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
  }

@media (min-width: 600px) {
    .card-columns {
        -webkit-column-count: 1;
                column-count: 1;
    }
}

@media (min-width: 800px) {
    .card-columns {
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media (min-width: 1200px) {
    .card-columns {
        -webkit-column-count: 3;
                column-count: 3;
    }
}
